.site-main {
    .main-header {
        width: 100%;
        z-index: 1000;
        position: absolute;
        background-color: transparent;

        .inner {
            @include media-breakpoint-down(lg) {
                display: none;
            }

            position: absolute;
            width: 100%;
            padding: 0px 0px;
            background: transparent;
            padding: 20px 0 20px;
            -webkit-transition: background-color 2s ease;
            -moz-transition: background-color 2s ease;
            -o-transition: background-color 2s ease;
            transition: background-color 2s ease;

            .navbar {
                padding-bottom: 0px;

                .dark-logo {
                    display: none;
                }

                .navbar-nav {
                    .nav-item {
                        padding-left: 25px;

                        a {
                            position: relative;
                            color: inherit;
                            text-decoration: none;
                            line-height: 24px;
                            cursor: pointer;
                        }

                        a:before,
                        a:after {
                            content: "";
                            position: absolute;
                            transition: transform 0.5s ease;
                        }

                        .nav-link {
                            color: #ffffff;
                            font-size: 15px;
                            font-weight: 600;
                            line-height: 20px;
                            //padding-bottom: 3px;
                            transition: all 0.3s ease;

                            &::after {
                                display: none;
                            }

                            &:hover {
                                color: #2ebff3;
                            }

                            &:before {
                                left: 0;
                                bottom: 0;
                                width: 100%;
                                height: 1px;
                                background: #2ebff3;
                                transform: scaleX(0);
                            }

                            &:hover:before {
                                transform: scaleX(0.9);
                            }
                        }

                        &.dropdown {
                            .dropdown-menu {
                                width: 100%;
                                left: 0;
                                right: 0;
                                background-color: #fff;
                                border-radius: 0px;
                                border: unset;
                                box-shadow: unset !important;
                                margin-top: 0px;
                                padding-top: 0;

                                .mega-content {
                                    padding: 30px 0 40px;

                                    .mega-menu-title-blue {
                                        color: #2ebff3;
                                        padding-bottom: 30px;
                                        padding-left: 25px;

                                        @include media-breakpoint-down(xl) {
                                            font-size: 20px;
                                        }
                                    }

                                    .mega-menu-title-orange {
                                        color: #f46922;
                                        padding-bottom: 30px;
                                        padding-left: 25px;
                                    }

                                    .mega-right-col {
                                        .mega-menu-title-blue {
                                            padding-left: 0px;
                                        }

                                        .card {
                                            border: unset;

                                            .mega-menu-image {
                                                min-height: 25vh;
                                                background-position: center center;
                                                background-repeat: no-repeat;
                                                background-size: cover;
                                            }

                                            .card-body {
                                                padding-top: 0px;

                                                .section-title {
                                                    color: #24292e;
                                                    font-size: 12px;
                                                    font-weight: 700;
                                                    text-transform: uppercase;
                                                    letter-spacing: 2px;
                                                    padding-bottom: 30px;
                                                }

                                                .post-title {
                                                    color: #24292e;
                                                    font-size: 20px;
                                                    font-weight: 600;
                                                }

                                                .post-description {
                                                    color: #24292e;
                                                    font-size: 12px;
                                                    font-weight: 400;
                                                }

                                                .readmore-link {
                                                    color: #f05f26;
                                                    font-size: 15px;
                                                    font-weight: 600;
                                                    text-decoration: none;
                                                    display: flex;
                                                    align-items: center;
                                                    transition: all 0.3s ease;

                                                    i {
                                                        font-size: 16px;
                                                        padding-right: 5px;
                                                    }

                                                    &:hover {
                                                        color: #2ebff3;
                                                    }
                                                }
                                            }
                                        }

                                        .job-positions {
                                            padding-top: 10px;
                                            padding-left: 15px;
                                            list-style-image: url("../assets/images/menu-arrow.png");

                                            .single-position {
                                                padding-bottom: 20px;

                                                a {
                                                    p {
                                                        margin-bottom: 0px;
                                                    }

                                                    .job-title {
                                                        color: #24292e;
                                                        font-size: 20px;
                                                        font-weight: 600;
                                                    }

                                                    .job-location {
                                                        color: #8d8d8d;
                                                        font-size: 15px;
                                                        font-weight: 600;
                                                    }
                                                }
                                            }
                                        }

                                        .view-all-open {
                                            list-style: none;
                                            padding-left: 0px;
                                        }

                                        .readmore-link {
                                            color: #f46922;
                                            font-size: 15px;
                                            font-weight: 600;
                                            text-decoration: none;
                                            display: flex;
                                            align-items: center;
                                            transition: all 0.3s ease;

                                            i {
                                                font-size: 16px;
                                                padding-right: 5px;
                                            }

                                            &:hover {
                                                color: #f05f26;
                                            }
                                        }
                                    }

                                    .mega-left-col {
                                        .mega-menu {
                                            .sub-menu-section {
                                                p {
                                                    color: #24292e;
                                                    font-size: 15px;
                                                    font-weight: 600;
                                                    margin-bottom: 0px;

                                                    @include media-breakpoint-down(xl) {
                                                        font-size: 13px;
                                                    }
                                                }

                                                .mega-sub-navbar-nav {
                                                    padding-top: 10px;
                                                    padding-inline-start: 17px;
                                                    list-style-image: url("../assets/images/menu-arrow.png");

                                                    .mega-sub-nav-item {
                                                        padding-bottom: 10px;

                                                        .mega-sub-nav-link {
                                                            color: #24292e;
                                                            font-size: 15px;
                                                            font-weight: 300;
                                                            transition: all 0.3s ease;

                                                            @include media-breakpoint-down(xl) {
                                                                font-size: 13px;
                                                            }

                                                            &:hover {
                                                                color: #2ebff3;
                                                            }
                                                        }
                                                    }
                                                }
                                            }

                                            .mega-navbar-nav {
                                                list-style: none;
                                                display: flex;
                                                flex-direction: row;
                                                flex-wrap: wrap;
                                                padding-left: 0px;

                                                .mega-nav-item {
                                                    margin: 10px 0;

                                                    .mega-link {
                                                        color: #24292e;
                                                        font-size: 15px;
                                                        font-weight: 500;
                                                        padding: 10px 25px;
                                                        transition: all 0.3s ease;

                                                        &:hover {
                                                            color: #2ebff3;
                                                        }
                                                    }
                                                }
                                            }

                                            .list-group {
                                                .list-group-item {
                                                    padding-left: 25px;
                                                    border: unset;
                                                    border-radius: 0px;

                                                    .mega-link {
                                                        color: #24292e;
                                                        font-size: 15px;
                                                        font-weight: 500;
                                                        transition: all 0.3s ease;

                                                        &:hover {
                                                            color: #2ebff3;
                                                        }
                                                    }
                                                }
                                            }
                                        }

                                        .readmore-link {
                                            color: #2ebff3;
                                            font-size: 15px;
                                            font-weight: 600;
                                            text-decoration: none;
                                            display: flex;
                                            align-items: center;
                                            padding-top: 20px;
                                            padding-left: 25px;
                                            transition: all 0.3s ease;

                                            i {
                                                font-size: 16px;
                                                padding-right: 5px;
                                            }

                                            &:hover {
                                                color: #f05f26;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            &.navbar-white {
                background: #fff !important;
                // transition-property: all;
                // transition-duration: 1s;
                //transition: background-color 1s ease;

                &:hover {
                    background-color: #fff;
                }

                .navbar {
                    .dark-logo {
                        display: block;
                    }

                    .white-logo {
                        display: none;
                    }
                }

                .navbar-nav {
                    .nav-item {
                        .nav-link {
                            color: #24292e !important;

                            &:hover {
                                color: #2ebff3 !important;
                            }
                        }
                    }
                }
            }

            .navbar-transparent {
                background: transparent !important;
            }

            &.affix {
                z-index: 1000;
                position: fixed;
                min-height: 80px;
                padding: 20px 0px 20px;
                background: #fff !important;

                .navbar {
                    .dark-logo {
                        display: block !important;
                    }

                    .white-logo {
                        display: none !important;
                    }

                    .navbar-nav {
                        .nav-item {
                            padding-left: 25px;

                            a {
                                position: relative;
                                color: inherit;
                                text-decoration: none;
                                line-height: 24px;
                                cursor: pointer;
                            }

                            a:before,
                            a:after {
                                content: "";
                                position: absolute;
                                transition: transform 0.5s ease;
                            }

                            .nav-link {
                                color: #24292e;
                                font-size: 15px;
                                font-weight: 600;
                                line-height: 20px;
                                padding-bottom: 3px;
                                transition: all 0.3s ease;

                                &::after {
                                    display: none;
                                }

                                &:hover {
                                    color: #2ebff3;
                                }

                                &:before {
                                    left: 0;
                                    bottom: 0;
                                    width: 100%;
                                    height: 1px;
                                    background: #2ebff3;
                                    transform: scaleX(0);
                                }

                                &:hover:before {
                                    transform: scaleX(0.9);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .mobile-header {
        width: 100%;
        z-index: 1000;
        position: absolute;
        background-color: transparent;

        .mobile-inner {
            display: none;

            @include media-breakpoint-down(lg) {
                display: block;
            }

            position: absolute;
            width: 100%;
            padding: 0px 0px;
            background: #fff;
            -webkit-transition: background-color 2s ease;
            -moz-transition: background-color 2s ease;
            -o-transition: background-color 2s ease;
            transition: background-color 2s ease;
            padding: 10px 0 10px;

            .navbar {
                // background: transparent;

                // .dark-logo {
                //     display: none;
                // }

                // .white-logo {
                //     display: block;
                // }

                .navbar-nav {
                    padding-top: 10px;

                    .nav-item {
                        .nav-link {
                            color: #24292e;
                            font-size: 15px;
                            font-weight: 500;
                            line-height: 20px;
                            transition: all 0.3s ease;
                            padding: 15px 15px;

                            &::after {
                                display: none;
                            }

                            &:hover {
                                color: #2ebff3;
                            }

                            &:focus {
                                color: #2ebff3;
                            }

                            &:active {
                                color: #2ebff3;
                            }
                        }

                        &.dropdown {
                            .dropdown-menu {
                                width: 100%;
                                //top: 52px;
                                left: 0;
                                right: 0;
                                background-color: #fff;
                                border-radius: 0px;
                                border: unset;
                                box-shadow: unset !important;
                                margin-top: 0px;

                                .mega-content {
                                    padding: 0px 0 10px;
                                    border-bottom: 1px solid #113559;

                                    .mega-menu {
                                        .mega-menu-title-blue {
                                            color: #2ebff3;
                                            padding-bottom: 5px;

                                            @include media-breakpoint-down(xl) {
                                                font-size: 20px;
                                            }
                                        }

                                        .mega-menu-title-orange {
                                            color: #f46922;
                                            padding-bottom: 5px;
                                        }

                                        .sub-menu-section {
                                            p {
                                                color: #24292e;
                                                font-size: 15px;
                                                font-weight: 600;
                                                margin-bottom: 0px;

                                                @include media-breakpoint-down(xl) {
                                                    font-size: 13px;
                                                }
                                            }

                                            .mega-sub-navbar-nav {
                                                padding-top: 10px;
                                                list-style-image: url("../assets/images/menu-arrow.png");

                                                .mega-sub-nav-item {
                                                    padding-bottom: 10px;

                                                    .mega-sub-nav-link {
                                                        color: #24292e;
                                                        font-size: 15px;
                                                        font-weight: 300;
                                                        transition: all 0.3s ease;

                                                        @include media-breakpoint-down(xl) {
                                                            font-size: 13px;
                                                        }

                                                        &:hover {
                                                            color: #2ebff3;
                                                        }
                                                    }
                                                }
                                            }
                                        }

                                        .mega-navbar-nav {
                                            list-style: none;
                                            display: flex;
                                            flex-direction: row;
                                            flex-wrap: wrap;
                                            padding-left: 0px;

                                            .mega-nav-item {
                                                margin: 10px 0;

                                                .mega-link {
                                                    color: #24292e;
                                                    font-size: 13px;
                                                    font-weight: 500;
                                                    padding: 10px 25px 10px 0px;
                                                    transition: all 0.3s ease;

                                                    &:hover {
                                                        color: #2ebff3;
                                                    }
                                                }
                                            }
                                        }

                                        .list-group {
                                            .list-group-item {
                                                border: unset;
                                                border-radius: 0px;
                                                padding-left: 0px;

                                                .mega-link {
                                                    color: #24292e;
                                                    font-size: 13px;
                                                    font-weight: 500;
                                                    transition: all 0.3s ease;

                                                    &:hover {
                                                        color: #2ebff3;
                                                    }
                                                }
                                            }
                                        }
                                    }

                                    .readmore-link {
                                        color: #2ebff3;
                                        font-size: 15px;
                                        font-weight: 600;
                                        text-decoration: none;
                                        display: flex;
                                        align-items: center;
                                        padding-top: 20px;
                                        transition: all 0.3s ease;

                                        i {
                                            font-size: 16px;
                                            padding-right: 5px;
                                        }

                                        &:hover {
                                            color: #f05f26;
                                        }
                                    }
                                }
                            }

                            &:hover {
                                .nav-link {
                                    color: #2ebff3;
                                }
                            }
                        }
                    }
                }

                .navbar-toggler {
                    border: unset;

                    &.hamburger {
                        .line {
                            width: 30px;
                            height: 2px;
                            background-color: #123A66;
                            display: block;
                            margin: 7px auto;
                            -webkit-transition: all 0.3s ease-in-out;
                            -o-transition: all 0.3s ease-in-out;
                            transition: all 0.3s ease-in-out;
                        }

                        &:hover {
                            cursor: pointer;
                        }

                        &#hamburger-1.is-active .line:nth-child(2) {
                            opacity: 0;
                        }

                        &#hamburger-1.is-active .line:nth-child(1) {
                            -webkit-transform: translateY(10px) rotate(45deg);
                            -ms-transform: translateY(10px) rotate(45deg);
                            -o-transform: translateY(10px) rotate(45deg);
                            transform: translateY(10px) rotate(45deg);
                        }

                        &#hamburger-1.is-active .line:nth-child(3) {
                            -webkit-transform: translateY(-8px) rotate(-45deg);
                            -ms-transform: translateY(-8px) rotate(-45deg);
                            -o-transform: translateY(-8px) rotate(-45deg);
                            transform: translateY(-8px) rotate(-45deg);
                        }
                    }

                    &:focus {
                        box-shadow: unset;
                    }
                }
            }

            &.affix {
                z-index: 1000;
                position: fixed;
                padding: 10px 0px 10px 0px;
                background: #fff !important;
                // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

                // .navbar {
                //     .dark-logo {
                //         display: block !important;
                //     }

                //     .white-logo {
                //         display: none !important;
                //     }
                // }
            }
        }
    }
}