//Button Styles
.button-wrapper {
    .btn {
        border-radius: 0px;
        &:focus {
            box-shadow: none;
        }

        &.outline-btn {
            border: 1px solid #2ebff3;
            padding: 8px 20px;
            transition: all .3s ease;

            a {
                color: #2ebff3;
                font-size: 20px;
                font-weight: 600;
                display: flex;
                align-items: center;

                @include media-breakpoint-down(xxl) {
                    font-size: 18px;
                }
            
                @include media-breakpoint-down(md) {
                    font-size: 15px;
                }

                i {
                    padding-right: 15px;
                }
            }

            &:hover {
                border: 1px solid #2ebff3;
                background-color: #2ebff3;

                a {
                    color: #fff;
                }
            }
        }

        &.filled-btn {
            background-color: #2ebff3;
            padding: 8px 25px;

            a {
                color: #fff;
                font-size: 20px;
                font-weight: 600;
                display: flex;
                align-items: center;

                @include media-breakpoint-down(xxl) {
                    font-size: 18px;
                }
            
                @include media-breakpoint-down(md) {
                    font-size: 15px;
                }

                i {
                    padding-right: 15px;
                }
            }

            &:hover {
                border: 1px solid #2ebff3;
                background-color: transparent;

                a {
                    color: #2ebff3;
                }
            }
        }

    }
}

.link-wrap {
    a {
        font-size: 20px;
        font-weight: 600;
        display: flex;
        align-items: center;

        @include media-breakpoint-down(xxl) {
            font-size: 18px;
        }

        @include media-breakpoint-down(md) {
            font-size: 15px;
        }

        svg{
            margin-right: 10px;
        }

        i {
            padding-right: 10px;
            font-weight: 100;
        }
    }
}

//Client Logo slider Styles
.clients-logo-section {
    // padding: 60px 0;
    background-color: #FAFAFA;

    .container{
        padding: 60px 0;

        @include media-breakpoint-down(xl) {
            padding: 40px 0;
        }
    
        @include media-breakpoint-down(md) {
            padding: 30px 0;
        }
    }

    .clients-heading {
        h3 {
            color: #24292E;
        }
    }

    .slider-wrapper {
        padding-top: 30px;
        @include media-breakpoint-down(md) {
            padding-top: 20px;
        }

        .clients-slider {
            .owl-stage {
                display: flex;
                align-items: center;

                .item {
                    .icon-wrap {
                        .client-logo {
                            width: 70%;
                            transition: filter .3s ease-in-out;
                            filter: grayscale(100%);

                            @include media-breakpoint-down(md) {
                                margin: 0 auto;
                            }

                            &:hover {
                                filter: none;
                            }
                        }
                    }
                }
            }
        }

        .clients-non-slider {
            .owl-stage {
                display: flex;
                align-items: center;

                .item {
                    .icon-wrap {
                        .client-logo {
                            width: 70%;
                            transition: filter .3s ease-in-out;
                            filter: grayscale(100%);

                            @include media-breakpoint-down(md) {
                                margin: 0 auto;
                            }

                            &:hover {
                                filter: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

