//Subscribe section styles
.subscribe {
    .container-fluid {
        .left-col {
            .content-wrap {
                padding-right: 120px;
                padding-left: 30%;
                @media (max-width: 1440px) { 
                    padding-left: 15%;
                 }
                 @include media-breakpoint-down(lg) { 
                    padding-right: 50px;
                  }
                  @include media-breakpoint-down(md) { 
                    padding: 0 20px;
                  }
            }

            .left-col-top {
                background-color: #2EBFF3;
                height: 30vh;
                display: flex;
                align-items: flex-end;
                padding-bottom: 40px;

                @include media-breakpoint-down(xl) {
                    height: 30vh;
                }

                @include media-breakpoint-down(md) {
                    height: 20vh;
                    padding-left: 0px;
                    padding-bottom: 0px;
                    align-items: center;
                    justify-content: center;
                }

                h3 {
                    color: #fff;
                    @include media-breakpoint-down(md) {
                        text-align: center;
                    }
                }
            }

            .left-col-bottom {
                background-color: #123A66;
                height: 10vh;
                display: flex;
                align-items: center;

                @include media-breakpoint-down(md) {
                    height: 15vh;
                    padding-left: 0px;
                    justify-content: center;
                }

                .content-wrap{
                    .hbspt-form{
                        form{
                            display: flex;
                            align-items: center;

                            .hs_email{
                                label{
                                    display: none;
                                }

                                .input{
                                    .hs-input{
                                        display: block;
                                        width: 100%;
                                        padding: 12px 25px;
                                        font-size: 1rem;
                                        font-weight: 400;
                                        line-height: 1.5;
                                        color: #212529;
                                        background-clip: padding-box;
                                        appearance: none;
                                        transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
                                        background-color: #123a66;
                                        border: unset;
                                        border-bottom: 1px solid #2ebff3;
                                        border-radius: 0;
                                        outline: none;

                                        @include media-breakpoint-down(md) {
                                            padding: 8px 25px;
                                        }

                                        &:focus{
                                            box-shadow: none;
                                        }

                                        &::-webkit-input-placeholder {
                                            /* Edge */
                                            color: #fff;
                                        }
                
                                        &:-ms-input-placeholder {
                                            /* Internet Explorer 10-11 */
                                            color: #fff;
                                        }
                
                                        &::placeholder {
                                            color: #fff;
                                        }
                                    }

                                    input:-internal-autofill-selected{
                                        background-color: #123A66 !important;
                                    }
                                }
                            }

                            .hs_submit{
                                .hs-button{
                                    border: 1px solid #2ebff3;
                                    background-color: transparent;
                                    padding: 8px 25px;
                                    color: #fff;
                                    font-size: 20px;
                                    font-weight: 500;

                                    &:focus{
                                        box-shadow: none;
                                    }

                                    @include media-breakpoint-down(md) {
                                        font-size: 16px;
                                    }
                                }
                            }
                        }

                        .submitted-message{
                            p{
                                color: #fff;
                                margin-bottom: 0px;
                            }
                        }
                    }
                }

                .input-group {
                    flex-wrap: nowrap;
                    align-items: end;
                    .form-control {
                        background-color: #123A66;
                        border: unset;
                        border-bottom: 1px solid #2ebff3;
                        border-radius: 0px;

                        &:focus {
                            box-shadow: none;
                        }

                        &::-webkit-input-placeholder {
                            /* Edge */
                            color: #fff;
                        }

                        &:-ms-input-placeholder {
                            /* Internet Explorer 10-11 */
                            color: #fff;
                        }

                        &::placeholder {
                            color: #fff;
                        }
                    }

                    .btn {

                        &:focus{
                            box-shadow: none;
                        }

                        &.sub-filled-btn {
                            border: 1px solid #2ebff3;
                            background-color: transparent;
                            padding: 8px 25px;
                            color: #fff;
                            font-size: 20px;
                            font-weight: 500;

                            @include media-breakpoint-down(md) {
                                font-size: 16px;
                            }
                        }
                    }

                    .wpcf7-spinner{
                        display: none;
                    }
                }

                h4 {
                    color: #fff;
                    @include media-breakpoint-down(md) {
                        text-align: center;
                    }
                }
            }
        }

        .right-col {
            img {
                width: 100%;
                height: 40vh;
                object-fit: cover;

                @include media-breakpoint-down(md) {
                    height: 25vh;
                }
            }
        }
    }
}

