.progress-bar-wrapper {
    position: fixed;
    top: 0px;
    left: 0;
    width: 100%;
    height: 0.35em;
    // z-index: 10001;
}

.progress {
   height: 100%;
   background: #2EBFF3;
   width: 0;
   // make animation less jittery
   transition: width .05s ease 0s;
}

